.conatiner {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: 600px;
  background: rgb(236, 209, 170);
}
.value {
  width: 400px;
  height: 60px;
  margin-top: 20px;
  margin: 0 auto;
  text-align: center;
  background: rgb(209, 168, 92);
}
.value h2 {
  text-transform: capitalize;
}
.staticItems h2 {
  padding-top: 10px;
  text-transform: capitalize;
}
.input {
  margin: 0 auto;
  border: none;
  outline: none;
  width: 390px;
  height: 50px;
  margin-top: 10px;
  padding-left: 10px;
}
.staticItems {
  margin: 0 auto;
  text-align: center;
  width: 400px;
  height: 60px;
  margin-top: 20px;
  margin-bottom: 20px;
  background: rgb(209, 168, 92);
}
